class App extends React.Component {
  constructor(props) {
    super(props);
    this.items = {};
  }

  render() {
    return <div className="app">
      <Categories></Categories>
    </div>
  }
}

export default App;

function Categories() {
  if (!firebase.apps.length) {
    firebase.initializeApp(firebaseConfig);
  }
  const db = firebase.firestore();

  let itemsByCategory = {"WF": {}, "PRIMARY": {}, "SECONDARY": {}, "KITGUN": {}, "MELEE": {}, "ZAW": {}, "AMP": {}, "SENTINEL": {}, "SENTINEL_WEAPONS": {}, "AW" : {}, "AW_MELEE": {}, "AW_GUN": {}, "CAT": {}, "DOG": {}, "MOA": {}, "KDRIVE": {}, "MECH": {}};
  let categoryComponents = [];
  const query = db.collection("items");
  const [items] = useCollectionData(query, { idField: 'id' });
  console.log(items);
  if (items) {
    items.forEach(item => {
      if (!itemsByCategory[item.category]) itemsByCategory[item.category] = {};
      itemsByCategory[item.category][item.id] = item;
      delete item.category;
    });

    categoryComponents = Object.keys(itemsByCategory).map(category => {
      let itemComponents = Object.keys(itemsByCategory[category]).map(name => {
        return <Item key={name} name={name}></Item>
      });
      return <div className="category" key={category}>
        <span className="category-name">{category}</span>
        {itemComponents}
      </div>
    })
  }
  return <>
    {categoryComponents}
  </>;
}