import React from 'react';

class Item extends React.Component {
    constructor(props) {
        super(props);
        this.state = { "mastered": false };
        this.toggleMasteredState = this.toggleMasteredState.bind(this);
    }

    render() {
        return (
            <div className="item" hidden={this.state.mastered}>
                <button className="item-name" onClick={this.toggleMasteredState}>{this.props.name}</button>
            </div>
        )
    }

    toggleMasteredState(event) {
        this.setState({
            mastered: !this.state.mastered
        });
    }
}


export default Item;